import React from 'react';
import { motion } from 'framer-motion';

const buttonVariants = {
  hover: {
    y: -2,
    scale: 1.05,
    transition: { type: 'spring', stiffness: 300, damping: 15 }
  },
  tap: {
    scale: 0.95
  }
};

const PublicationCardComponent = React.forwardRef(({ pub, className }, ref) => (
  <div ref={ref} className={className}>
    <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4">
      <div className="flex-1">
        <h3 className="text-xl font-semibold text-white mb-2">{pub.title}</h3>
        <p className="text-gray-400 mb-1">{pub.authors}</p>
        <p className="text-blue-400 font-medium">{pub.journal} • {pub.year}</p>
      </div>
      <motion.a
        href={`https://doi.org/${pub.doi}`}
        target="_blank"
        rel="noopener noreferrer"
        className="px-6 py-3 bg-gradient-to-r from-blue-500 to-blue-600 text-white font-semibold rounded-full text-center lg:w-auto cursor-pointer"
        variants={buttonVariants}
        whileHover="hover"
        whileTap="tap"
      >
        View Paper
      </motion.a>
    </div>
  </div>
));

const PublicationCard = motion(PublicationCardComponent);

export default PublicationCard; 